<!-- UserComment.vue -->
<template>
  <div class="comments-section">
    <div class="comments-header">
      <i class="bi bi-chat-right-text" style="font-size: 20px; margin-right: 10px;"></i>
      <h3 style="font-size: 17px; font-family: 'KB_C2', sans-serif; text-align: left;margin: 0;">ÎåìÍ∏Ä ({{ comments.length }})</h3>
    </div>
  <div class="comment-input" style=" width: 100%;">
    <textarea 
  class="input-base"
  placeholder="ÎåìÍ∏ÄÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî"
  v-model="newComment"
  @keydown.enter.prevent="addComment" 
  style="
    font-family: 'KB_C2', sans-serif; 
    font-size: 18px; 
    width: 100%; 
    height: 80px; 
    resize: none;
    padding: 10px;
    text-align: left;
  "
></textarea>
    <button 
    class="btn-yellow btn-yellow:hover"
      @click="addComment"  @keydown.enter.prevent="addComment" 
      style="font-family: 'KB_C2', sans-serif; font-size: 18px; width: 100px; height: 80px;color: #4b4a4a;"
    >Îì±Î°ù</button>
  </div>

  
    <div v-if="comments && comments.length">
      <div class="comment" v-for="comment in comments" :key="comment.mtip_reply_no">
        <img
              v-if="comment && comment.user_no"
              :src="profileImageUrl(comment.user_no)"
              class="profile-small my-3"
              alt="Profile Picture"
              @error="setDefaultImage"
            />
        <div class="comment-content">
          <div class="comment-meta">
            <span class="comment-user" style="font-family: 'KB_C2', sans-serif; font-size: 18px; margin-right: 15px;">{{ comment.user_name }}</span>
            <span class="comment-date" style="font-family: 'KB_C2', sans-serif; font-size: 14px;">{{ formatDate(comment.mtip_reply_date) }}</span>
            <div class="comment-actions" style="margin-left: 50px;">
              <div v-if="comment.user_no === loginInfo.user_no">
        <button v-if="!comment.isEditing" class="edit-button" @click="startEditing(comment)" style="color:#714319;">ÏàòÏ†ï</button>
        <button v-if="comment.isEditing" class="edit-button" @click="finishEditing(comment)" style="color:#714319;">ÏàòÏ†ïÏôÑÎ£å</button>
        &nbsp;
        <button class="delete-button" @click="deleteComment(comment.mtip_reply_no)" style="color: red;">ÏÇ≠Ï†ú</button>
      </div>
    </div>
  </div>
        <div v-if="comment.isEditing">
          <textarea 
            v-model="comment.editingContent"
            class="input-base"
            style="
              font-family: 'KB_C2', sans-serif;
              min-height: 30px;
              height: auto;
              font-size: 18px;
              width: 100%;
              resize: none;
              overflow-y: hidden;
              line-height: 2;
              text-align: left;
              box-sizing: border-box;"></textarea>
        </div>
        <div v-else class="comment-text" style="font-family: 'KB_C2', sans-serif; font-size: 18px; margin-right: 1%; text-align:left;">
          {{ comment.mtip_reply_content || comment.editingContent }}
        </div>
        </div>   
      </div>
    </div>
    <div v-else style="font-family: 'KB_C2', sans-serif; font-size: 15px; margin-top: 100px;">
      Ìï¥Îãπ Í≤åÏãúÍ∏ÄÏùò ÎåìÍ∏ÄÏù¥ ÏóÜÏäµÎãàÎã§.
    </div>
  </div>
</template>

<script>
// import api from '@/api/axios';
import { mapActions, mapGetters } from 'vuex';

export default {
  name: 'UserComment',
  props: {
    loginInfo: {
      type: Object,
      required: true,
    },
    mtip_board_no: {
      type: Number,
      required: true,
    },
  },
  data() {
    return {
      // comments: [], // ÏàòÏ†ïÎêú Î∂ÄÎ∂Ñ
      newComment: '',
      // editingCommentId: null, // Ï∂îÍ∞ÄÎêú Î∂ÄÎ∂Ñ
    };
  },
 
  created() { // Ï∂îÍ∞ÄÎêú Î∂ÄÎ∂Ñ
  console.log('UserComment component created ÌôïÏù∏Ìï¥Î≥¥Í∏∞');
  console.log('mtip_board_no ÌôïÏù∏ Ï§ë:', this.mtip_board_no);
  console.log('loginInfo ÌôïÏù∏ Ï§ë:', this.loginInfo);
  console.log('API URL ÌôïÏù∏ Ï§ë:', process.env.VUE_APP_API_URL);
  // this.$store.dispatch('mtipReply/fetchComments', this.mtip_board_no);
  console.log('mtip_board_no:', this.mtip_board_no); // Ïù¥ Î∂ÄÎ∂ÑÏùÑ Ï∂îÍ∞ÄÌïòÏó¨ Í∞íÏù¥ Ïò¨Î∞îÎ•¥Í≤å Ï†ÑÎã¨ÎêòÏóàÎäîÏßÄ ÌôïÏù∏Ìï©ÎãàÎã§.
  this.fetchComments(this.mtip_board_no);
  },


  computed: {
    ...mapGetters('mtipReply', ['getComments']),
    comments() {
      console.log('Computed comments:', this.getComments);
      return this.getComments;
    },
  },
  watch: {
  comments: {
    deep: true,
    handler() {
      console.log('ÎåìÍ∏ÄÏù¥ ÏóÖÎç∞Ïù¥Ìä∏ÎêòÏóàÏäµÎãàÎã§.');
    }
  }
},

  methods: {
    ...mapActions('mtipReply', ['fetchComments', 'addComment', 'updateComment', 'deleteComment']),

  
    //ÌîÑÎ°úÌïÑ ÏÇ¨ÏßÑ Î∂àÎü¨Ïò§Í∏∞
    profileImageUrl(user_no) {
      if (user_no!=null) {
        if (process.env.NODE_ENV === 'development') {
          return `${process.env.VUE_APP_API_URL}/profile/${user_no}.jpg`;
        } else {
          return `/profile/${user_no}.jpg`;
        }
      }
      return ''; // ÎòêÎäî Í∏∞Î≥∏ Ïù¥ÎØ∏ÏßÄ URL
    },

    getInitials(name) {
    return name ? name.charAt(0).toUpperCase() : '?';
    },

    startEditing(comment) {
      comment.isEditing = true;
      comment.editingContent = comment.mtip_reply_content;
    },

    async fetchComments(mtip_board_no) {
    await this.$store.dispatch('mtipReply/fetchComments', mtip_board_no);
  },

  async addComment() {
    if (!this.newComment.trim()) return;
    const commentData = {
        user_no: this.loginInfo.user_no,
        user_name: this.loginInfo.user_name,
        mtip_board_no: this.mtip_board_no,
        mtip_reply_content: this.newComment.trim(),
    };
    console.log('Comment data to be sent:', commentData);  // Ï∂îÍ∞ÄÎêú Î°úÍ∑∏
    try {
        const response = await this.$store.dispatch('mtipReply/addComment', commentData);
        console.log('ÎåìÍ∏Ä Îì±Î°ù ÏùëÎãµ:', response);  // Ï∂îÍ∞ÄÎêú Î°úÍ∑∏
        this.newComment = '';
        // window.location.reload()

         // fetchComments Ìò∏Ï∂úÌïòÏó¨ ÎåìÍ∏Ä Î™©Î°ùÏùÑ Îã§Ïãú Î∂àÎü¨ÏòµÎãàÎã§.
         await this.fetchComments(this.mtip_board_no);
    } catch (error) {
        console.error('ÎåìÍ∏Ä Îì±Î°ù Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§:', error);  // ÏóêÎü¨ Î∞úÏÉù Ïãú Î°úÍ∑∏
    }
},


    // Í∏∞Î≥∏ ÌîÑÎ°úÌïÑ ÏÇ¨ÏßÑ
    setDefaultImage(event) {
      event.target.src = require('@/assets/img/test.png');
    },
    //ÎÇ†Ïßú Î≥ÄÌòï
    formatDate(dateString) {
      const options = { 
        year: 'numeric', 
        month: '2-digit', 
        day: '2-digit',
        hour: '2-digit',
        minute: '2-digit',
        hour12: false
      };
      return new Date(dateString).toLocaleString('ko-KR', options);
    },
    goToModifyView(comment) {
    // ÏàòÏ†ï Í∏∞Îä• Íµ¨ÌòÑ, ÌïÑÏöîÏóê Îî∞Îùº Îß§Í∞úÎ≥ÄÏàò Ï†ÑÎã¨
    this.newComment = comment.mtip_reply_content;
    this.editingCommentId = comment.mtip_reply_no;
  },
  async finishEditing(comment) {
  if (!comment.editingContent.trim()) return;

  const commentData = {
    mtip_reply_no: comment.mtip_reply_no,
    mtip_reply_content: comment.editingContent.trim(),
  };

  try {
    const updatedComment = await this.$store.dispatch('mtipReply/updateComment', commentData);
    console.log('ÏóÖÎç∞Ïù¥Ìä∏Îêú ÎåìÍ∏Ä:', updatedComment);  // Ï∂îÍ∞ÄÎêú Î°úÍ∑∏

     // ÏÑúÎ≤ÑÏóêÏÑú Î∞òÌôòÎêú Îç∞Ïù¥ÌÑ∞Î°ú ÎåìÍ∏Ä ÎÇ¥Ïö© ÏóÖÎç∞Ïù¥Ìä∏
    comment.mtip_reply_content = updatedComment.mtip_reply_content || comment.editingContent.trim();
    comment.isEditing = false;
  } catch (error) {
    console.error('ÎåìÍ∏Ä ÏàòÏ†ï Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§:', error);
    // Ïò§Î•ò Ï≤òÎ¶¨ (Ïòà: ÏÇ¨Ïö©ÏûêÏóêÍ≤å ÏïåÎ¶º)
  }
},
  async deleteComment(mtip_reply_no) {
    try {
      await this.$store.dispatch('mtipReply/deleteComment', mtip_reply_no);
    } catch (error) {
      console.error('ÎåìÍ∏Ä ÏÇ≠Ï†ú Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§:', error);
    }
  },
  },
  // beforeRouteUpdate(to, from, next) {
  //   if (to.params.mtip_board_no !== from.params.mtip_board_no) {
  //     this.$store.dispatch('mtipReply/fetchComments', to.params.mtip_board_no);
  //   }
  //   next();
  // },
};
</script>

<style scoped>
