<template>
  <div class="app-container">
    <div v-if="notice" class="content cards">
      <div class="header">
        <div class="back-container">
          <button class="back-button" @click="goBack">
            <span class="arrow">‚ùÆ</span> Ïù¥Ï†Ñ
          </button>
        </div>
        <div class="actions">
          <!-- Î°úÍ∑∏Ïù∏Ìïú ÏÇ¨Ïö©ÏûêÍ∞Ä Í∏Ä ÏûëÏÑ±ÏûêÏù∏ Í≤ΩÏö∞ -->
          <div
            v-if="
              isLoggedIn && notice.user_no === loginInfo.user_no && !isChecked
            "
          >
            <button class="edit-button mr-3" @click="goToModifyView">
              ÏàòÏ†ï
            </button>
            <button class="delete-button mr-3" @click="deleteNotice">
              ÏÇ≠Ï†ú
            </button>
          </div>
          <div
            v-if="
              isLoggedIn &&
              loginInfo.user_is_admin &&
              !loginInfo.user_is_manager &&
              isChecked
            "
          >
            <button class="revoke-button" @click="mtip_complainBack">
              Ïã†Í≥†Ï∑®Ìïò
            </button>
            &nbsp;&nbsp;
            <button class="delete-button" @click="deleteNoticeAdmin">
              ÏÇ≠Ï†ú
            </button>
          </div>
        </div>
      </div>
      <div class="content">
        <span v-if="isNew(notice?.mtip_board_date)" class="new-label">NEW</span>
        <h1 class="title">{{ notice?.mtip_board_title }}</h1>
        <div class="meta">
          <span class="author">{{ notice?.user_name }}</span>
          <span class="date">{{ formatDate(notice?.mtip_board_date) }}</span>
        </div>
        <div class="main-content">
          <div class="body">
            <pre><p>{{ notice?.mtip_board_content }}</p></pre>
            <!-- Ï§Ñ Î∞îÍøà -->
          </div>
          <div class="file cards">
            <div style="display: flex; align-items: center">
              <h2 style="margin-right: 10px">Ï≤®Î∂ÄÌååÏùº</h2>
              <span
                v-if="!notice.mtip_board_file"
                style="
                  color: #4b4a4a;
                  font-family: 'KB_C2', sans-serif;
                  margin-left: 2%;
                  white-space: nowrap;
                "
                >ÌååÏùºÏù¥ Ï°¥Ïû¨ÌïòÏßÄ ÏïäÏäµÎãàÎã§.</span
              >
            </div>
            <div v-if="notice.mtip_board_file" style="margin-top: 10px">
              <a
                @click.prevent="downloadFile"
                href="#"
                class="file-download-link"
              >
                {{ getDisplayFileName(notice?.mtip_board_file) }}
              </a>
            </div>
          </div>
        </div>
        <div class="stats-container">
          <div class="icon-container">
            <div class="views-icon">
              <i class="bi bi-eye"></i>
            </div>
            <div class="views-text">{{ notice?.mtip_board_hit }}</div>
          </div>
          <div class="icon-container" @click="toggleLike">
            <div class="heart-icon">
              <i
                :class="['bi', isLiked ? 'bi-heart-fill' : 'bi-heart']"
                :style="{ color: isLiked ? '#dc3545' : 'inherit' }"
              ></i>
            </div>
            <div class="views-text">{{ notice?.mtip_board_like }}</div>
          </div>
        </div>
      </div>
      <div class="content-container mx-auto">
        <div class="actions">
          <div v-if="isLoggedIn && loginInfo.user_is_admin && isChecked"></div>
          <div v-else>
            <span class="alert-icon">üö®</span>
            <button class="report-button" @click="mtip_complain">
              Ïã†Í≥†ÌïòÍ∏∞
            </button>
          </div>
        </div>
        <hr class="divider" />

        <UserComment
          v-if="isNoticeLoaded"
          :login-info="loginInfo"
          :mtip_board_no="notice.mtip_board_no"
        />
      </div>
    </div>
    <div v-else>
      <p v-if="isLoading">Loading...</p>
      <p v-else>No data available</p>
    </div>
  </div>
</template>

<script>
import api from '@/api/axios';
import { mapActions, mapGetters, mapState } from 'vuex';
import Swal from 'sweetalert2';
import UserComment from '@/components/m-tip/UserComment';
import mtipReply from '@/store/mtip/mtipReply';

export default {
  name: 'M_TipDetailView',
  props: ['mtip_board_no'],
  modules: {
    mtipReply,
  },
  data() {
    return {
      isLoading: true,
      isNoticeLoaded: false,
      comments: [], // commentsÎ•º Ï¥àÍ∏∞Ìôî
    };
  },
  watch: {
    '$route.params.mtip_board_no': {
      immediate: true,
      handler(newId, oldId) {
        if (newId && newId !== oldId) {
          this.fetchNoticeDetail(newId);
          this.$store.commit('mtipReply/CLEAR_COMMENTS');
          this.$store.dispatch('mtipReply/fetchComments', newId);
        }
      },
    },
  },
  components: {
    UserComment,
  },
  methods: {
    ...mapActions('mtipBoard', [
      'fetchNoticeDetail',
      'likePost',
      'unlikePost',
      'checkLikeStatus',
    ]),

    async toggleLike() {
      console.log('Ïó¨Í∏∞ÍπåÏßÄÏò§Í∏¥Ïò¥');
      if (!this.loginInfo) {
        alert('Î°úÍ∑∏Ïù∏Ïù¥ ÌïÑÏöîÌï©ÎãàÎã§.');
        return;
      }
      console.log('Î°úÍ∑∏Ïù∏ÌôïÏù∏ÏôÑÎ£°');
      if (!this.notice) {
        console.error('Notice data is not available');
        return;
      }
      console.log('ÎÖ∏Ìã∞Ïä§ÌôïÏù∏ÏôÑÎ£°!');
      try {
        const action = this.isLiked ? 'unlikePost' : 'likePost';
        console.log('Ïù¥Ï†ú ÏóëÏãúÏò§Ïä§ Í∞àÍ±∞ÏûÑ.');
        const result = await this.$store.dispatch(`mtipBoard/${action}`, {
          mtip_board_no: this.notice.mtip_board_no,
          user_no: this.loginInfo.user_no,
        });

        console.log('ÏÑúÎ≤ÑÏóêÏÑú Î∞õÏùÄ Ï¢ãÏïÑÏöî ÏÉÅÌÉú:', result);

        // Ï¢ãÏïÑÏöî ÏÉÅÌÉú ÏóÖÎç∞Ïù¥Ìä∏
        await this.$store.dispatch('mtipBoard/checkLikeStatus', {
          mtip_board_no: this.notice.mtip_board_no,
          user_no: this.loginInfo.user_no,
        });

        // ÏÑúÎ≤ÑÏóêÏÑú Î∞õÏïÑÏò® Ï¢ãÏïÑÏöî ÏàòÎ•º ÏßÅÏ†ë ÏÑ§Ï†ï
        if (result !== -1) {
          this.notice.mtip_board_like = result.isLiked; // ÏÑúÎ≤ÑÏóêÏÑú Î∞õÏùÄ Ï¢ãÏïÑÏöî ÏàòÎ•º Í∑∏ÎåÄÎ°ú Î∞òÏòÅ
        }
      } catch (error) {
        console.error('Error toggling like:', error);
      }
    },
    async deleteNotice() {
      Swal.fire({
        title: 'Í≤ΩÍ≥†',
        text: 'Ï†ïÎßêÎ°ú ÏÇ≠Ï†úÌïòÏãúÍ≤†ÏäµÎãàÍπå?',
        icon: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#4b4a4a',
        cancelButtonColor: '#bd2c3a',
        confirmButtonText: 'ÌôïÏù∏',
        cancelButtonText: 'Ï∑®ÏÜå',
        reverseButtons: false,
        allowOutsideClick: false,
        scrollbarPadding: false,
      }).then(async (result) => {
        if (result.isConfirmed) {
          try {
            await api.delete(`/mtip/delete/${this.notice.mtip_board_no}`);
            Swal.fire({
              title: 'Í≤åÏãúÍ∏Ä ÏÇ≠Ï†ú ÏôÑÎ£å',
              text: 'Í≤åÏãúÍ∏ÄÏù¥ ÏÇ≠Ï†ú ÎêòÏóàÏäµÎãàÎã§.',
              icon: 'success',
              allowOutsideClick: false,
              scrollbarPadding: false,
            }).then(() => {
              this.$router.push('/M_TipListView');
            });
          } catch (error) {
            console.error('Í≤åÏãúÍ∏Ä ÏÇ≠Ï†ú Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.', error);
            Swal.fire({
              title: 'Í≤åÏãúÍ∏Ä ÏÇ≠Ï†ú Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.',
              icon: 'error',
              allowOutsideClick: false,
              scrollbarPadding: false,
            });
          }
        }
      });
    },

    async deleteNoticeAdmin() {
      Swal.fire({
        title: 'Í≤ΩÍ≥†',
        text: 'Ï†ïÎßêÎ°ú ÏÇ≠Ï†úÌïòÏãúÍ≤†ÏäµÎãàÍπå?',
        icon: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#4b4a4a',
        cancelButtonColor: '#bd2c3a',
        confirmButtonText: 'ÌôïÏù∏',
        cancelButtonText: 'Ï∑®ÏÜå',
        reverseButtons: false,
        allowOutsideClick: false,
        scrollbarPadding: false,
      }).then(async (result) => {
        if (result.isConfirmed) {
          try {
            await api.delete(`/mtip/delete/${this.notice.mtip_board_no}`);
            Swal.fire({
              title: 'Í≤åÏãúÍ∏Ä ÏÇ≠Ï†ú ÏôÑÎ£å',
              text: 'Í≤åÏãúÍ∏ÄÏù¥ ÏÇ≠Ï†ú ÎêòÏóàÏäµÎãàÎã§.',
              icon: 'success',
              allowOutsideClick: false,
              scrollbarPadding: false,
            }).then(() => {
              this.$router.push('/mTipMainAdminView');
            });
          } catch (error) {
            console.error('Í≤åÏãúÍ∏Ä ÏÇ≠Ï†ú Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.', error);
            Swal.fire({
              title: 'Í≤åÏãúÍ∏Ä ÏÇ≠Ï†ú Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.',
              icon: 'error',
              allowOutsideClick: false,
              scrollbarPadding: false,
            });
          }
        }
      });
    },

    mtip_complain() {
      if (
        this.notice.mtip_complain === 1 ||
        this.notice.mtip_complain === true
      ) {
        Swal.fire({
          title: 'Í≤ΩÍ≥†',
          text: 'Ïù¥ÎØ∏ Ïã†Í≥†Îêú Í≤åÏãúÍ∏Ä ÏûÖÎãàÎã§.',
          icon: 'error',
          showCancelButton: true,
          confirmButtonColor: '#4b4a4a',
          cancelButtonColor: '#bd2c3a',
          confirmButtonText: 'ÌôïÏù∏',
          cancelButtonText: 'Ï∑®ÏÜå',
          reverseButtons: false,
        });
        return;
      }

      Swal.fire({
        title: 'Ïã†Í≥†',
        text: 'Ï†ïÎßêÎ°ú Ïù¥ Í≤åÏãúÍ∏ÄÏùÑ Ïã†Í≥†ÌïòÏãúÍ≤†ÏäµÎãàÍπå?',
        icon: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#4b4a4a',
        cancelButtonColor: '#bd2c3a',
        confirmButtonText: 'ÌôïÏù∏',
        cancelButtonText: 'Ï∑®ÏÜå',
        reverseButtons: false,
      }).then(async (result) => {
        if (result.isConfirmed) {
          try {
            await api.post(`/mtip/complain/${this.notice.mtip_board_no}`);
            Swal.fire(
              'Ïã†Í≥†ÏôÑÎ£å',
              'Ï†ïÏÉÅÏ†ÅÏúºÎ°ú Í≤åÏãúÍ∏Ä Ïã†Í≥†Í∞Ä ÏôÑÎ£åÎêòÏóàÏäµÎãàÎã§.',
              'success'
            ).then(() => {
              this.$router.push('/M_TipListView');
            });
          } catch (error) {
            console.error('Ïã†Í≥†ÌïòÍ∏∞ Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.', error);
            Swal.fire('Ïã†Í≥†ÌïòÍ∏∞ Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.', '', 'error');
          }
        }
      });
    },

    mtip_complainBack() {
      Swal.fire({
        title: 'Ïã†Í≥†Ï∑®Ìïò',
        text: 'Ï†ïÎßêÎ°ú Ïù¥ Í≤åÏãúÍ∏ÄÏùÑ Ïã†Í≥†Ï∑®Ìïò ÌïòÏãúÍ≤†ÏäµÎãàÍπå?',
        icon: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#4b4a4a',
        cancelButtonColor: '#bd2c3a',
        confirmButtonText: 'ÌôïÏù∏',
        cancelButtonText: 'Ï∑®ÏÜå',
        reverseButtons: false,
      }).then(async (result) => {
        if (result.isConfirmed) {
          try {
            await api.post(`/mtip/complainBack/${this.notice.mtip_board_no}`);
            Swal.fire(
              'ÏÑ±Í≥µ',
              'Ï†ïÏÉÅÏ†ÅÏúºÎ°ú Í≤åÏãúÍ∏Ä Ïã†Í≥†Ï∑®ÌïòÍ∞Ä ÏôÑÎ£åÎêòÏóàÏäµÎãàÎã§.',
              'success'
            ).then(() => {
              this.$router.push('/mTipMainAdminView'); //Ïó¨Í∏∞ Î≥ÄÍ≤ΩÌï¥ÏïºÎåê
            });
          } catch (error) {
            console.error('Ïã†Í≥†Ï∑®Ìïò Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.', error);
            Swal.fire('Ïã†Í≥†Ï∑®Ìïò Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.', '', 'error');
          }
        }
      });
    },

    showAlert(message, icon) {
      this.$swal({
        title: message,
        icon: icon,
        confirmButtonText: 'ÌôïÏù∏',
        confirmButtonColor: '#4b4a4a',
        allowOutsideClick: false,
        allowEscapeKey: false,
        allowEnterKey: false,
        stopKeydownPropagation: false,
        scrollbarPadding: false,
        backdrop: true,
        didOpen: () => {
          document.body.classList.add('no-scroll');
          document.documentElement.style.overflow = 'hidden';
        },
        willClose: () => {
          document.body.classList.remove('no-scroll');
          document.documentElement.style.overflow = '';
        },
      });
    },

    getDisplayFileName(fileName) {
      const UUID_LENGTH = 36 + 1;

      if (!fileName || fileName.length <= UUID_LENGTH) {
        return fileName;
      }

      if (fileName.charAt(UUID_LENGTH - 1) === '_') {
        return fileName.substring(UUID_LENGTH);
      }

      return fileName;
    },

    goBack() {
      this.$router.go(-1);
    },
    goToModifyView() {
      console.log('modify ÌôîÎ©¥ÏúºÎ°ú ÎÑòÍ∏¥ Ï†ïÎ≥¥:', this.notice.mtip_board_no);
      this.$router.push({
        name: 'm_TipModifyView',
        params: { mtip_board_no: this.notice.mtip_board_no },
      });
    },
    isNew(dateString) {
      const today = new Date();
      const noticeDate = new Date(dateString);
      const diffTime = Math.abs(today - noticeDate);
      const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
      return diffDays <= 7;
    },
    formatDate(dateString) {
      console.log('Formatting date:', dateString);
      if (!dateString) return 'Date not available';

      const date = new Date(dateString);

      if (isNaN(date.getTime())) {
        console.error('Invalid date string:', dateString);
        return ' ‚Ü∫ ÎåìÍ∏Ä Îì±Î°ù Ï§ë';
      }

      const options = {
        year: 'numeric',
        month: '2-digit',
        day: '2-digit',
        hour: '2-digit',
        minute: '2-digit',
        hour12: false,
      };

      return date.toLocaleString('ko-KR', options);
    },

    async downloadFile() {
      try {
        console.log('Í∏ÄÏì∞Í∏∞ ÏÉÅÏÑ∏Î≥¥Í∏∞ fileName :', this.notice.mtip_board_file);
        const fileName = encodeURIComponent(this.notice.mtip_board_file);
        const response = await api({
          url: `/mtip/downloadGuide/${fileName}`,
          method: 'GET',
          responseType: 'blob',
        });

        const url = window.URL.createObjectURL(new Blob([response.data]));
        const link = document.createElement('a');
        link.href = url;
        link.setAttribute('download', this.notice.mtip_board_file);
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
      } catch (error) {
        console.error('ÌååÏùº Îã§Ïö¥Î°úÎìú Ï§ë Ïò§Î•ò Î∞úÏÉù:', error);
        this.showAlert('ÌååÏùº Îã§Ïö¥Î°úÎìú Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.', 'error');
      }
    },
    async fetchNoticeDetail(id) {
      this.isLoading = true;
      this.isNoticeLoaded = false;
      await this.$store.dispatch('mtipBoard/fetchNoticeDetail', id);

      const likedPosts = JSON.parse(localStorage.getItem('likedPosts')) || {};
      this.isLiked = likedPosts[this.notice.mtip_board_no] || false;

      this.isLoading = false;
      this.isNoticeLoaded = true;
    },
  },
  computed: {
    ...mapGetters('login', ['getLoginInfo', 'getIsChecked']),
    ...mapGetters('mtipBoard', ['getNotice']),
    ...mapState('login', ['loginInfo']),
    ...mapState('mtipBoard', ['likedPosts']),

    notice() {
      console.log('Ïó¨Í∏∞Ïó¨Í∏∞', this.getNotice);
      return this.getNotice || null;
    },
    loginInfo() {
      return this.getLoginInfo;
    },
    isChecked() {
      return this.getIsChecked;
    },
    isLoggedIn() {
      return !!this.loginInfo;
    },
    mileNo() {
      return this.$route.params.notice.mtip_board_no;
    },
    isLiked() {
      return this.$store.getters['mtipBoard/isPostLiked'](
        this.loginInfo.user_no,
        this.notice.mtip_board_no
      );
    },
  },
  mounted() {
    const noticeId = this.$route.params.mtip_board_no;
    if (noticeId) {
      this.fetchNoticeDetail(noticeId);
      this.$store.commit('mtipReply/CLEAR_COMMENTS'); // ÎåìÍ∏Ä Ï¥àÍ∏∞Ìôî
      this.$store.dispatch('mtipReply/fetchComments', noticeId);
    }
  },
};
</script>

